<template>
  <div id="main1"
       style="width: 100%; height: 500px;">
  </div>
</template>
<script>
import * as echarts from 'echarts';
export default {
  name: "fenneishuju",
  props: ['datas'],
  data () {
    return {
    }
  },
  methods: {
    initEcharts () {
      var myChart = echarts.init(document.getElementById('main1'));
      var option = {
        // color: ['#C991FA', '#FCBD5E', '#9D92FF'],
        title: {
          text: "浏览趋势堆叠图",
          subtext: this.datas.names
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#6a7985'
            }
          }
        },
        legend: {
          data: ['历史', '上周', '本周',]
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'category',
            boundaryGap: false,
            data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日']
          }
        ],
        yAxis: [
          {
            type: 'value'
          }
        ],
        series: [
          {
            name: '历史',
            type: 'line',
            stack: '总量',
            areaStyle: {},
            emphasis: {
              focus: 'series'
            },
            data: this.datas.lishi
          },
          {
            name: '上周',
            type: 'line',
            stack: '总量',
            areaStyle: {},
            emphasis: {
              focus: 'series'
            },
            data: this.datas.shangzhou
          },
          {
            name: '本周',
            type: 'line',
            stack: '总量',
            areaStyle: {},
            emphasis: {
              focus: 'series'
            },
            data: this.datas.benzhou
          },



        ]
      };
      myChart.setOption(option);
      // 监听window窗口大小变化的事件
      window.onresize = function () {
        myChart.resize()
      }
    },

  },
  mounted () {
    console.log(this.datas);
    //  this.initEcharts()
    this.$nextTick(() => {
      //  执行echarts方法
      this.initEcharts()
    })
  },


};
</script>
